import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { LoginPage } from '../login/login';


@IonicPage()
@Component({
  selector: 'page-dashboard',
  templateUrl: 'dashboard.html',
})
export class DashboardPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,public utiltiy:UtiltiyProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DashboardPage');
  }

  logout(){
    this.utiltiy.removeStorage("token");
    this.utiltiy.removeStorage("roleID");
    this.navCtrl.setRoot(LoginPage);

  }

}



