import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LoginPageModule } from '../pages/login/login.module';
import { AuthProvider } from '../providers/auth/auth';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { DashboardPageModule } from '../pages/dashboard/dashboard.module';
import { UtiltiyProvider } from '../providers/utiltiy/utiltiy';
import { PrestartPageModule } from '../pages/prestart/prestart.module';
import { AddprestartPageModule } from '../pages/addprestart/addprestart.module';
import { PrestartProvider } from '../providers/prestart/prestart';
import { CrudProvider } from '../providers/crud/crud';
import { CabsandPrestartPageModule } from '../pages/cabsand-prestart/cabsand-prestart.module';
import { CabsandAddprestartPageModule } from '../pages/cabsand-addprestart/cabsand-addprestart.module';
import { KlmrFuelPageModule } from '../pages/klmr-fuel/klmr-fuel.module';
import { KlmrFuelTruckPageModule } from '../pages/klmr-fuel-truck/klmr-fuel-truck.module';
import { KlmrFuelAddPageModule } from '../pages/klmr-fuel-add/klmr-fuel-add.module';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage
  ],
  imports: [
    BrowserModule,
    LoginPageModule,
    DashboardPageModule,
    PrestartPageModule,
    AddprestartPageModule,
    CabsandPrestartPageModule,
    CabsandAddprestartPageModule,
    KlmrFuelPageModule,
    KlmrFuelTruckPageModule,
    KlmrFuelAddPageModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthProvider,
    UtiltiyProvider,
    PrestartProvider,
    CrudProvider
  ]
})
export class AppModule {}



