import { Component } from '@angular/core';
import { NavController,Events } from 'ionic-angular';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  currentClass:any = null;
  constructor(public navCtrl: NavController,public utility:UtiltiyProvider,public events:Events) {
    this.getStatus();
    this.utility.setStorage("currentClass","home");
    this.events.publish('currentClass',"home");
    this.currentClass = "home";
  }

  async getStatus(){
    // let currentGroup = await this.utility.getStorage("currentClass");
    // if(currentGroup){
    // this.currentClass = currentGroup;

    // }
  }

  selectType(type){
    this.currentClass = type;
    console.log("typw",this.currentClass);
    this.utility.setStorage("currentClass",type);
    this.events.publish('currentClass',type);


  }

}



