import { Injectable } from '@angular/core';
import { LoadingController, Loading, ToastController, LoadingOptions, AlertController, Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';

@Injectable()
export class UtiltiyProvider {
  loading: Loading;
  constructor(private loadingCtrl: LoadingController, private toastCtrl: ToastController, private storage: Storage,private  alertCtrl: AlertController) {

  }

  startLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent',
      cssClass: 'loader'
    });
    this.loading.present();
  }
  // For stopLoading
  stopLoading() {
    this.loading.dismiss().catch(() => this.loading.dismiss());
  }

  // show toast
  toaster(text) {
    const toast = this.toastCtrl.create({
      message: text,
      duration: 3000,
      position: 'bottom'
    });
    toast.present();
  }

  // For set data inside the local Storage
  setStorage(key, value) {
    this.storage.set(key, value);

  }
  // For get data from local Storage
  async getStorage(key) {
    return this.storage.get(key).then((val) => {
      console.log("value is",val)
      return val
    })
  }

  removeStorage(key) {
    this.storage.remove(key);
  }

  async showDialog() {
    return new Promise(async (resolve) => {

    const confirm = await this.alertCtrl.create({
      title: 'Delete',
      message: 'Do you want to delete this record',
      buttons: [
        {
          text: 'No',
          handler: () => {
            console.log('Disagree clicked');
            return resolve(false);

          }
        },
        {
          text: 'Yes',
          handler: () => {
            console.log('Agree clicked');
            return resolve(true);

          }
        }
      ]
    });
    await  confirm.present();
  });
  }

}



