import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { CabsandAddprestartPage } from '../cabsand-addprestart/cabsand-addprestart';

@IonicPage()
@Component({
  selector: 'page-cabsand-prestart',
  templateUrl: 'cabsand-prestart.html',
})
export class CabsandPrestartPage {
  prestartList: any = [];
  appColor:String;
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }

  ionViewDidLoad() {
    this.getappColor();
    this.getprestartList();
  }
  async getappColor(){
    this.appColor = await this.utiltiy.getStorage("primaryColor");
  }

  getprestartList() {
    this.prestartList = [];
    this.utiltiy.startLoading();
    this.api.get("/tansix_prestart/get", false)
      .then((result: any) => {
        console.log("result", result)
        this.prestartList = result.result;
        this.utiltiy.stopLoading();
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  checkStatus(string) {
    return string.includes("Faulty");
  }

  async deleteRecord(id) {
    let status = await this.utiltiy.showDialog();
    if (status) {
      this.deletePrestart(id);
    }
    console.log("status", status);
  }


  deletePrestart(id) {
    this.api.delete(`/tansix_prestart/delete/${id}`, false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.getprestartList();
        }

      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }
  
 addPrestart(){
   this.navCtrl.push(CabsandAddprestartPage, { param: null });
 }
 updatePrestart(prestart){
   this.navCtrl.push(CabsandAddprestartPage, { param: prestart });
 }

}



