import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { PrestartPage } from '../prestart/prestart';

@IonicPage()
@Component({
  selector: 'page-addprestart',
  templateUrl: 'addprestart.html',
})
export class AddprestartPage {
  fleetList: any = [];
  selectedFleet: any;
  selectedFleetItems: any = [];
  selectOptions: any;
  createPrestart: FormGroup;
  isUpdate: boolean = false;
  params: any = {};
  appColor:String;
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider, private formBuilder: FormBuilder) {
    this.selectOptions = {
      mode: 'ios'
    };
    this.params = this.navParams.get('param');
    if (this.params) {
      this.isUpdate = true;

    }

    this.createPrestart = this.formBuilder.group({
      user_id: new FormControl('', [Validators.required]),
      fleet_no: new FormControl('', [Validators.required]),
      date: new FormControl('', [Validators.required]),
      hours: new FormControl('', [Validators.required]),
      kilometers: new FormControl('', [Validators.required]),
      drivername: new FormControl('', [Validators.required]),
      location: new FormControl('', [Validators.required]),
      oils_added: new FormControl('', [Validators.required]),
    });
    console.log("param", this.params)
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad');
    this.getappColor();
    this.getMetaData();

  }

  async getappColor(){
    this.appColor = await this.utiltiy.getStorage("primaryColor");
  }
  async getMetaData() {
    this.fleetList = await this.getFleetList();
    if (this.params) {
      this.regenerateForm();
    }
  }

  async getFleetList() {
    this.fleetList = [];
    this.utiltiy.startLoading();
    return this.api.get("/prestart/fleet/get", false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          return result.result;
        }
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
        return [];
      })
  }

  regenerateForm() {
    console.log("hurrayyy");
    this.createPrestart.patchValue({
      user_id: this.params.user_id,
      date: this.params.date,
      hours: this.params.hours,
      kilometers: this.params.kilometers,
      drivername: this.params.drivername,
      location: this.params.location,
      oils_added: this.params.oils_added,
    });
    this.selectedFleet = this.params.fleet_no;
    this.fleetChanged(this.params.fleet_no);
  }



  fleetChanged(fleetData) {
    console.log(this.fleetList, "fleetName", fleetData)
    let index = this.fleetList.findIndex(list_ => list_.fleet_no.toLowerCase() == fleetData.toLowerCase());
    if (index == -1) {
      return;
    }

    this.createPrestart.patchValue({
      fleet_no: fleetData,
    });
    const vehicles = this.fleetList[index].vehicles;
    let status = vehicles.includes(",");
    let vehicleList = [];
    console.log("status", status)

    if (status) {
      vehicleList = vehicles.split(",");
    } else {
      vehicleList.push(vehicles);
    }
    this.generateFleetItemList(vehicleList);
  }


  generateFleetItemList(list) {
    this.selectedFleetItems = [];
    list.forEach((vehicle_, i) => {
      var obj = {}
      if (this.isUpdate) {
        let status = this.params.item_status.includes(",");
        if (status) {
          let item_status = this.params.item_status.split(",");
          let comment = this.params.comment.split(",");
          obj = {
            "name": vehicle_,
            "status": item_status[i],
            "comments": comment[i],
          }
        } else {
          obj = {
            "name": vehicle_,
            "status": this.params.item_status,
            "comments": this.params.comment,
          }
        }
      } else {
        obj = {
          "name": vehicle_,
          "status": null,
          "comments": "",
        }
      }
      this.selectedFleetItems.push(obj);
    });
    console.log("vehicleList", this.selectedFleetItems);

  }

  submit() {
    console.log("this.credentialsForm.value", this.createPrestart.value)
    let items = "";
    let item_status = "";
    let comment = "";

    let formKeys = Object.keys(this.createPrestart.value);

    this.selectedFleetItems.forEach((element) => {
      items = items + element.name + ","
      item_status = item_status + element.status + ","
      comment = comment + element.comments + ","
    });
    items = items.slice(0, items.length - 1);
    item_status = item_status.slice(0, item_status.length - 1);
    comment = comment.slice(0, comment.length - 1);

  

    if (this.isUpdate) {
      let string = "";
      formKeys.forEach(key => {
        if(string.length == 0){
          string = string+""+key+"="+this.createPrestart.value[key];
        }else{
          string = string+"&"+key+"="+this.createPrestart.value[key];
        }
      });
      let body = string+"&items[]=" + `${items}` +"&item_status[]=" + `${item_status}`+"&comment[]=" + `${comment}`;
      console.log("string-1",body);
      this.updatePrestart(body, this.params.id);
    } else {
      let formData: FormData = new FormData();
      formKeys.forEach(key => {
        formData.append(key, this.createPrestart.value[key]);
      });
      formData.append("items[]", items);
      formData.append("item_status[]", item_status);
      formData.append("comment[]", comment);
      this.addPrestart(formData);
    }



  }

  addPrestart(formData) {
    this.api.post(`/prestart/store`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.navCtrl.setRoot(PrestartPage);
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  updatePrestart(formData, id) {
    this.api.put(`/prestart/update/${id}`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.navCtrl.setRoot(PrestartPage);
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  stringFromArray() {

  }

}



