import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class CrudProvider {
  baseUrl: any;
  constructor(public http: HttpClient) {
    this.baseUrl = "https://cotest.online/klmr_cabsand/index.php/api"; //  live
  }


  get(endpoint, tokenRequired) {
    return new Promise((resolve, reject) => {
      this.http.get(this.baseUrl + endpoint).subscribe(data => {
        resolve(data);
      }, err => {
        reject(err);
        console.log("ERER:", err);
      });
    });
  }

  delete(endpoint, tokenRequired) {
    return new Promise((resolve, reject) => {
      this.http.delete(this.baseUrl + endpoint).subscribe(data => {
        resolve(data);
      }, err => {
        reject(err);
        console.log("ERER:", err);
      });
    });
  }
  

  post(endpoint, tokenRequired, payload) {
    return new Promise((resolve, reject) => {
      this.http.post(this.baseUrl + endpoint, payload).subscribe(data => {
        resolve(data);
      }, err => {
        reject(err);
        console.log("ERER:", err);
      });
    });
  }

  put(endpoint, tokenRequired, payload) {
    return new Promise((resolve, reject) => {
     
      this.http.put(this.baseUrl + endpoint,  payload, {
        headers: new HttpHeaders({
          'Content-Type': 'application/x-www-form-urlencoded'
        })
      }).subscribe(data => {
        resolve(data);
      }, err => {
        reject(err);
        console.log("ERER:", err);
      });
    });
  }

}


