import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { KlmrFuelAddPage } from '../klmr-fuel-add/klmr-fuel-add';

@IonicPage()
@Component({
  selector: 'page-klmr-fuel',
  templateUrl: 'klmr-fuel.html',
})
export class KlmrFuelPage {
  fuelList: any = [];
  appColor:String;
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }

  ionViewDidLoad() {
    this.getappColor();
    this.getfuelList();

  }

  async getappColor(){
    this.appColor = await this.utiltiy.getStorage("primaryColor");
  }


  getfuelList() {
    this.fuelList = [];
    this.utiltiy.startLoading();
    this.api.get("/prestart/fuel/fuel/get", false)
      .then((result: any) => {
        console.log("result", result)
        this.fuelList = result.result;
        this.utiltiy.stopLoading();
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }


  async deleteRecord(id) {
    let status = await this.utiltiy.showDialog();
    if (status) {
      this.deletePrestart(id);
    }
    console.log("status", status);
  }


  deletePrestart(id) {
    this.api.delete(`/prestart/fuel/fuel/delete/${id}`, false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.getfuelList();
        }

      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }
  
  addfuel(){
   this.navCtrl.push(KlmrFuelAddPage, { param: null });
 }
 updatePrestart(prestart){
   this.navCtrl.push(KlmrFuelAddPage, { param: prestart });
 }

}



